@use 'sass:color';

// Baptist church brand colors
$primary-green: #399344;
$primary-blue: #429aca;
$accent-yellow: #eac338;
$white: #ffffff;
$light-gray: #f8f9fa;
$dark-text: #333333;

// Hero Section
.hero {
  background: linear-gradient(135deg, $primary-green 0%, $primary-blue 100%);
  color: $white;
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="50" cy="50" r="2" fill="white" opacity="0.1"/></svg>') repeat;
    animation: float 20s ease-in-out infinite;
  }
  
  .hero-content {
    position: relative;
    z-index: 2;
    text-align: center;
    padding: 4rem 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .hero-title {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    color: $white;
    
    @media (max-width: 768px) {
      font-size: 2rem;
    }
  }
  
  .hero-subtitle {
    font-size: 1.25rem;
    margin-bottom: 2rem;
    opacity: 0.95;
    font-weight: 300;
  }
  
  // Hero Status & Countdown
  .hero-status {
    margin: 2rem 0;
    padding: 1.5rem;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.2);
    
    .live-indicator {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 0.5rem;
      font-size: 1.2rem;
      
      .live-badge {
        background: #ff4444;
        padding: 0.3rem 0.8rem;
        border-radius: 20px;
        font-weight: 600;
        animation: pulse 2s infinite;
      }
    }
    
    .countdown-display {
      text-align: center;
      
      .countdown-label {
        display: block;
        font-size: 1rem;
        opacity: 0.9;
        margin-bottom: 0.5rem;
      }
      
      .countdown-time {
        display: block;
        font-size: 1.5rem;
        font-weight: 600;
        color: $accent-yellow;
      }
    }
  }
  
  .hero-buttons {
    display: flex;
    gap: 1rem;
    justify-content: center;
    flex-wrap: wrap;
    
    .btn {
      padding: 1rem 2rem;
      font-size: 1.1rem;
      min-width: 150px;
    }
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

// Welcome Section
.welcome {
  background-color: $white;
  
  .lead {
    font-size: 1.2rem;
    font-style: italic;
    color: $primary-blue;
    margin-bottom: 2rem;
    font-weight: 300;
  }
  
  p {
    font-size: 1.1rem;
    line-height: 1.8;
    max-width: 800px;
    margin: 0 auto 1rem;
  }
}

// Upcoming Events Preview
.upcoming-events {
  background-color: $light-gray;
  
  .events-preview {
    display: grid;
    gap: 1.5rem;
    margin: 2rem 0;
    
    @media (min-width: 768px) {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  }
  
  .event-preview-card {
    background: $white;
    border-radius: 15px;
    padding: 1.5rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    
    &:hover {
      transform: translateY(-3px);
    }
    
    .event-date {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-bottom: 1rem;
      
      .event-icon {
        font-size: 1.5rem;
      }
      
      .event-time {
        font-weight: 600;
        color: $primary-blue;
      }
    }
    
    .event-info {
      h4 {
        color: $primary-green;
        margin-bottom: 0.5rem;
        font-size: 1.2rem;
      }
      
      p {
        color: #666;
        margin-bottom: 1rem;
        line-height: 1.5;
      }
      
      .event-meta {
        display: flex;
        gap: 1rem;
        font-size: 0.9rem;
        color: #888;
        flex-wrap: wrap;
      }
    }
  }
}



// Info Cards Section
.info-cards {
  background-color: $white;
  
  .cards-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin-top: 3rem;
  }
  
  .card {
    background: $white;
    padding: 2rem;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    flex: 0 1 300px;
    max-width: 400px;
    border: 2px solid transparent;
    
    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
      border-color: $primary-blue;
    }
    
    .card-icon {
      font-size: 3rem;
      margin-bottom: 1rem;
      display: block;
    }
    
    h3 {
      color: $primary-green;
      margin-bottom: 1rem;
      font-size: 1.5rem;
    }
    
    p {
      color: #666;
      line-height: 1.6;
      margin-bottom: 1.5rem;
    }
    
    .card-link {
      color: $primary-blue;
      text-decoration: none;
      font-weight: 600;
      transition: color 0.3s ease;
      
      &:hover {
        color: $primary-green;
      }
    }
  }
}

// Latest News Preview
.latest-news {
  background-color: $light-gray;
  
  .news-preview {
    display: grid;
    gap: 1.5rem;
    margin: 2rem 0;
    
    @media (min-width: 768px) {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  }
  
  .news-preview-card {
    background: $white;
    border-radius: 15px;
    padding: 1.5rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    
    &:hover {
      transform: translateY(-3px);
    }
    
    .news-date {
      font-size: 0.9rem;
      color: $primary-blue;
      font-weight: 600;
      margin-bottom: 0.5rem;
    }
    
    h4 {
      color: $primary-green;
      margin-bottom: 0.5rem;
      font-size: 1.2rem;
    }
    
    p {
      color: #666;
      margin-bottom: 1rem;
      line-height: 1.5;
    }
    
    .news-category {
      display: inline-block;
      background: rgba($primary-blue, 0.1);
      color: $primary-blue;
      padding: 0.3rem 0.8rem;
      border-radius: 20px;
      font-size: 0.8rem;
      font-weight: 600;
    }
  }
}

// Contact Info Section
.contact-info {
  background: linear-gradient(135deg, rgba($primary-green, 0.1) 0%, rgba($primary-blue, 0.1) 100%);
  
  .contact-content {
    text-align: center;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .contact-details {
    margin-top: 2rem;
    display: grid;
    gap: 1.5rem;
    
    @media (min-width: 768px) {
      grid-template-columns: 1fr;
    }
  }
  
  .contact-item {
    background: $white;
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    gap: 1rem;
    
    strong {
      color: $primary-green;
      min-width: 140px;
      text-align: left;
    }
    
    span {
      color: $dark-text;
      flex: 1;
      text-align: left;
    }
    
    @media (max-width: 768px) {
      flex-direction: column;
      text-align: center;
      
      strong, span {
        text-align: center;
        min-width: auto;
      }
    }
  }
}

// Button Styles
.btn {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  cursor: pointer;
  
  &.btn-primary {
    background: $primary-green;
    color: $white;
    
    &:hover {
      background: color.adjust($primary-green, $lightness: -10%);
      transform: translateY(-2px);
    }
  }
  
  &.btn-secondary {
    background: $primary-blue;
    color: $white;
    
    &:hover {
      background: color.adjust($primary-blue, $lightness: -10%);
      transform: translateY(-2px);
    }
  }
  
  &.btn-outline {
    background: transparent;
    color: $primary-blue;
    border-color: $primary-blue;
    
    &:hover {
      background: $primary-blue;
      color: $white;
      transform: translateY(-2px);
    }
  }
}

// Responsive Grid
.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col {
  flex: 1;
  padding: 0 15px;
}

// Utilities
.text-center {
  text-align: center;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  
  @media (max-width: 768px) {
    padding: 0 1rem;
  }
}

.section {
  padding: 4rem 0;
  
  h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: $primary-green;
    
    @media (max-width: 768px) {
      font-size: 2rem;
    }
  }
}
